<template>
    <body id="poster">
        <el-form class="login-container" label-position="left" label-width="80px">
            <h3 class="login_title">
                User Login
                <el-button @click="toRegister()">Register Here</el-button>
            </h3>
            <el-form-item label="username">
                <el-input type="text" v-model="LoginForm.adopter_name" autocomplete="off" placeholder="username"></el-input>
            </el-form-item>
            <el-form-item label="password">
                <el-input type="password" v-model="LoginForm.password" autocomplete="off" placeholder="password"></el-input>
            </el-form-item>
            <el-form-item>
                <el-button type="primary" style= "width: 100%;background: #505458;border:none;" v-on:click="Login">Submit</el-button>
            </el-form-item>
        </el-form>
    </body>
  </template>
  
  <script>
  
  export default {
    name: 'Login',
    data() {
      return {
        LoginForm: {
          adopter_name: '',
          password: '',
        }
      }
    },
    methods: {
      Login() {
        // console.log('submit!',this.LoginForm);
        const headers ={
            'Content-Type':'text/plain',
        }
        this.axios.post("http://127.0.0.1:5002//api/recommend/signin",this.LoginForm,{headers:headers}).then((resp) =>{
            let data= resp.data;
            if(data.code=='ER001'){
                this.$message({
                showClose: true,
                message: 'You typed invalid username/password',
                type: 'error',
                });
            };
            if(data.code=='SU001'){
                sessionStorage.setItem("loginStatus",!!resp.data.data)
                sessionStorage.setItem("userInfo",JSON.stringify(resp.data))
                this.$message({
                showClose: true,
                message: 'Welcome to dog adoption system!',
                type: 'success'
                });
                this.$router.push({path:"/HomeView"})
            }
        });
      },
      toRegister(){
        this.$router.push({path:'/Register'})
      }
    }
  }
  </script>

<style>
 #poster{
    background-position: center;
    height: 100%;
    width:100%;
    background-size: contain;
    position: fixed;
    background-image: url("../assets/1.png")
 }
body{
    margin:0px;
    padding:0px;
}
