<template>
    <div id="register_poster">
        <el-form :model="ruleForm" :rules="rules" ref="ruleForm" label-width="200px" class="register-container">
            <h3 class="register_title">
                <el-button @click="toSign()">Sign In Here</el-button>
                New User Registration
            </h3>
            <el-form-item label="create your username:" prop="adopter_name">
                <el-input v-model="ruleForm.adopter_name" placeholder="username" ></el-input>
            </el-form-item>
            <el-form-item label="create your password:" prop="password">
                <el-input v-model="ruleForm.password" type='password' placeholder="password"></el-input>
            </el-form-item>
            <h4>
                Please help us understand your dog adoption preferences!
            </h4>
            <el-form-item label="Gender of dog:" prop="prefer_gender">
                <el-checkbox-group v-model="ruleForm.prefer_gender">
                <el-checkbox label="1" name="type">Male</el-checkbox>
                <el-checkbox label="0" name="type">Female</el-checkbox>
                </el-checkbox-group>
            </el-form-item>
            <el-form-item label="Dog Age Category:" prop="prefer_age_group">
                <el-checkbox-group v-model="ruleForm.prefer_age_group">
                <el-checkbox label="1" value="1" name="type">puppy</el-checkbox>
                <el-checkbox label="2" value="2" name="type">teen/young adult</el-checkbox>
                <el-checkbox label="3" value="3" name="type">adult/senior</el-checkbox>
                </el-checkbox-group>
            </el-form-item>
            <el-form-item label="Personality Description:" prop="personality_preference">
                <el-input type="textarea" v-model="ruleForm.personality_preference"></el-input>
            </el-form-item>
            <h4>
              Help us to understand you too!
            </h4>
            <el-form-item label="Type of Housing:" prop="accomodation">
                <el-radio-group v-model="ruleForm.accomodation">
                <el-radio label="1" value=1>HDB</el-radio>
                <el-radio label="2" value=0>Condominium</el-radio>
                <el-radio label="3" value=0>Landed</el-radio>
                <el-radio label="4" value=0>Rental</el-radio>
                </el-radio-group>
            </el-form-item>
            <el-form-item label="Any kids at home:" prop="kid">
                <el-radio-group v-model="ruleForm.kid">
                <el-radio label="0">Yes</el-radio>
                <el-radio label="1">No</el-radio>
                </el-radio-group>
            </el-form-item>
            <el-form-item label="Any Elderly at home:" prop="Elderly">
                <el-radio-group v-model="ruleForm.Elderly">
                <el-radio label="0">Yes</el-radio>
                <el-radio label="1">No</el-radio>
                </el-radio-group>
            </el-form-item>
            <el-form-item label="Other pets at home:" prop="other_dog">
                <el-radio-group v-model="ruleForm.other_dog">
                <el-radio label="0">Yes</el-radio>
                <el-radio label="1">No</el-radio>
                </el-radio-group>
            </el-form-item>
            <el-form-item label="Dogs w.medical condition:" prop="medical">
                <el-radio-group v-model="ruleForm.medical">
                <el-radio label="1">I don't mind</el-radio>
                <el-radio label="0">No, thanks</el-radio>
                </el-radio-group>
            </el-form-item>
            <el-form-item label="Experience with dogs:" prop="experience">
                <el-radio-group v-model="ruleForm.experience">
                <el-radio label="1">Yes</el-radio>
                <el-radio label="0">No</el-radio>
                </el-radio-group>
            </el-form-item>
            <el-form-item label="Window grills at home:" prop="window">
                <el-radio-group v-model="ruleForm.window">
                <el-radio label="1">Yes</el-radio>
                <el-radio label="0">No</el-radio>
                </el-radio-group>
            </el-form-item>
            <el-form-item>
                <el-button type="primary" @click="submitForm('ruleForm')" style= "width: 45%;background: #505458;border:none;">Confirm</el-button>
                <el-button @click="resetForm('ruleForm')" style= "width: 45%;">Form Reset</el-button>
            </el-form-item>
        </el-form>
    </div>
  </template>
  
  <script>
  
  export default {
    name: 'Register',
    data() {
      return {
        ruleForm: {
            adopter_name:'',
            password:'',
            accomodation:'',
            prefer_gender:[],
            prefer_age_group:[],
            personality_preference:'',
            kid:'',
            Elderly:'',
            other_dog:'',
            medical:'',
            experience:'',
            window:''
        },
        rules: {
        adopter_name: [
            { required: true, message: 'please type your username', trigger: 'blur' }
          ],
          password: [
            { required: true, message: 'please type your password', trigger: 'blur' }
          ],
          accomodation: [
            { required: true, message: 'please choose your accommodation', trigger: 'change' }
          ],
          prefer_gender: [
            { type: 'array', required: true, message: 'please choose at least one gender', trigger: 'change' }
          ],
          prefer_age_group: [
            { type: 'array', required: true, message: 'please choose at least one type of age', trigger: 'change' }
          ],
          personality_preference: [
            { required: true, message: 'please type what kind of dog you want', trigger: 'blur' }
          ]
        }
      };
    },
    methods: {
      submitForm(formName) {
        this.$refs[formName].validate((valid) => {
          if (valid) {
            console.log(this.ruleForm)
            const headers ={
            'Content-Type':'text/plain',
            }
            this.axios.post("http://127.0.0.1:5002//api/recommend/signup",this.ruleForm,{headers:headers}).then((resp) =>{
                let data= resp.data;
                console.log(data)
                if(data.code=='ER002'){
                    this.$message({
                    showClose: true,
                    message: 'The username is used, please change one',
                    type: 'error',
                    });
                };
                if(data.code=='SU002'){
                    this.sign = {};
                    this.$message({
                    showClose: true,
                    message: 'You have created your account!',
                    type: 'success'
                    });
                    this.$router.push({path:"/"})
                }
            })
          } else {
            console.log('error submit!!');
            return false;
          }
        });
      },
      resetForm(formName) {
        this.$refs[formName].resetFields();
      },
      toSign(){
        this.$router.push({path:'/'})
      }
    }
  }
  </script>
  
