<template>
  <div id = 'home-poster'>
        <el-form class="home-container" label-position="left" label-width="0px">
            <h2 class="home_title" style="margin:10px;padding:0;">
                Welcome to Find Your Dog!
            </h2>
            <div style="line-height:25px;">
              <i>The one-stop app to adopt a dog.</i><br/>
              <i>Click on the button below to begin</i>
            </div>
            <p></p>
            <el-form-item>
                <el-button type="primary" :loading="loadingbut" style= "width: 100%;border:none;" v-on:click="ToRecommendation">{{loadingbuttext}}</el-button>
            </el-form-item>
            <el-form-item>
                <el-button style="width: 100%;" v-on:click="toSign">Sign Out</el-button>
            </el-form-item>
        </el-form>
  </div>
</template>

<script>
export default {
  name: 'HomeView',
  data() {
      return {
        loadingbut:false,
	      loadingbuttext:'Dog Recommendation',
      }
  },  
  methods:{
    ToRecommendation(){
      var user_info = JSON.parse(sessionStorage.getItem('userInfo'))
      this.loadingbut = true;
			this.loadingbuttext = 'Processing...';
      console.log('submit!',user_info.data)
      const headers ={
            'Content-Type':'text/plain',
      }
      this.axios.post("http://127.0.0.1:5002//api/recommend/adopter_new_recommend",user_info.data,{headers:headers}).then ((resp)=>{
          let data= resp.data;
          console.log(data)
          this.loadingbut = false;
				  this.loadingbuttext = 'Dog Recommendation';
          if(data["dog_0"]==undefined){
            // this.$message({
            //     showClose: true,
            //     message: 'Please give us more details to make recommendation!',
            //     type: 'error',
            //     });
            this.$alert('Unable to find a match. Please consider changing preferences or typing more details in the Personality Description box', 'Message Alert', {
                    confirmButtonText: 'Confirm',
                    callback: action => {
                      this.$message({
                        type: 'error',
                        message: `action: ${ action }`
                      });
                    }
                  });
            this.$router.push({path:"/Preference"})
          }
          else{
            this.$message({
                showClose: true,
                message: 'Recommend Successfully!',
                type: 'success'
                });
          sessionStorage.setItem("dogInfo",JSON.stringify(resp.data))
            // dog_index update
          this.$emit("dog_item")
          this.$router.push({path:"/recommendation"})
        }
      })
    },
    toSign(){
        this.$router.push({path:'/'})
    }
  }
}
</script>

